import { Container } from './styles';
import externalLink from "../../assets/external-link.svg";
import ScrollAnimation from "react-animate-on-scroll";
import reactIcon from '../../assets/react-icon.svg'
import vueIcon from "../../assets/vue-icon.svg";
import { useState } from 'react';

const allBlogs = [
  {
    title: "8 Tips SEO untuk Web Developer Pemula",
    description: "Di era digital ini, rezeki bisa datang dari mana saja...",
    link: "https://www.hipwee.com/sukses/8-tips-seo-untuk-blogger-ini-harus-kamu-kuasai-agar-pembacamu-berjuta-juta/",
    imgAlt: "Blog1"
  },
  {
    title: "6 Alasan Memilih Resign Sebelum Dapat Kerja Baru",
    description: "Yuk, kita kupas tuntas kenapa kadang, resign dulu baru cari lagi...",
    link: "https://www.hipwee.com/motivasi/resign-sebelum-punya-pekerjaan-baru/",
    imgAlt: "Blog2"
  },
  {
    title: "Tips Hidup di Era Digital, Hati-Hati Bahaya Sosial..!",
    description: "Beberapa penelitian yang pernah dilakukan membuktikan jika sebuah hubungan...",
    link: "https://www.hipwee.com/hubungan/bahaya-sosial-media-dalam-kehidupan-cintamu/",
    imgAlt: "Blog3"
  },
  {
    title: "Story of Dicoding",
    description: "Semangat dan kerja keras para developer untuk mempersembahkan karya terbaik mereka...",
    link: "https://www.dicoding.com/blog/",
    imgAlt: "Blog4"
  },
  {
    title: "Apa Itu SMO (Social Media Optimization)",
    description: "SMO adalah Social Media Optimization, merupakan teknik untuk mengoptimalkan social media...",
    link: "https://codingstudio.id/blog/smo-adalah/",
    imgAlt: "Blog5"
  },
  {
    title: "Model Bisnis D2C: Pengertian, Manfaat dan Strateginya",
    description: "D2C adalah sebuah model bisnis yang memungkinkan produsen menjual produk secara langsung...",
    link: "https://codingstudio.id/blog/d2c-adalah/",
    imgAlt: "Blog6"
  },
  {
    title: "Proxy Server: Pengertian, Fungsi, Manfaat dan Jenisnya",
    description: "Proxy server adalah salah satu layanan jaringan yang saat ini banyak digunakan...",
    link: "https://codingstudio.id/blog/proxy-server-adalah/",
    imgAlt: "Blog7"
  },
  {
    title: "What Is Vue",
    description: "Apa itu Vue JS? Vue JS merupakan framework JavaScript yang dapat digunakan...",
    link: "https://vuejs.org/",
    imgAlt: "Blog8",
    icon: vueIcon
  },
  {
    title: "What Is React",
    description: "React adalah perpustakaan JavaScript open-source yang digunakan untuk membuat antarmuka pengguna...",
    link: "https://react.dev/",
    imgAlt: "Blog9",
    icon: reactIcon
  }
];

export function Blog() {
  const [currentPage, setCurrentPage] = useState(1);
  const blogsPerPage = 3;

  const startIndex = (currentPage - 1) * blogsPerPage;
  const currentBlogs = allBlogs.slice(startIndex, startIndex + blogsPerPage);

  const loadMore = () => {

    if (startIndex + blogsPerPage >= allBlogs.length) {

      setCurrentPage(1);
    } else {

      setCurrentPage(prevPage => prevPage + 1);
    }
  };

  return (
    <Container id="blog">
      <h2>Collection Of Articles</h2>
      <div className="blogs">
        {currentBlogs.map((blog, index) => (
          <ScrollAnimation key={index} animateIn="fadeIn" duration={2}>
            <div className="blog">
              <h3>
                {blog.title}
                {blog.icon && <img className={blog.icon === vueIcon ? 'vue' : 'react'} src={blog.icon} alt={blog.imgAlt} />}
              </h3>
              <p>{blog.description}</p>
              <a href={blog.link} target='_blank' rel="noopener noreferrer">
                <img src={externalLink} alt={blog.imgAlt} />
              </a>
            </div>
          </ScrollAnimation>
        ))}
      </div>

      {/* Only show "More" button if there are more blogs left to load */}
      {currentBlogs.length < allBlogs.length && (
        <button className="more" onClick={loadMore}>More</button>
      )}

      {/* Pagination */}
      <div className="pagination">
        <button 
          onClick={() => setCurrentPage(1)} 
          disabled={currentPage === 1}>
          1
        </button>
        <button 
          onClick={() => setCurrentPage(2)} 
          disabled={currentPage === 2 || allBlogs.length <= 3}>
          2
        </button>
        <button 
          onClick={() => setCurrentPage(3)} 
          disabled={currentPage === 3 || allBlogs.length <= 6}>
          3
        </button>
      </div>
    </Container>
  );
}
