import React, { useState, ChangeEvent, MouseEvent, KeyboardEvent, useEffect } from 'react';

// Tipe untuk pesan
interface Message {
  text: string;
  sender: 'user' | 'bot';
}

const ChatBot: React.FC = () => {
  const [isOpen, setIsOpen] = useState<boolean>(false);
  const [messages, setMessages] = useState<Message[]>([]);
  const [input, setInput] = useState<string>('');
  const [hasNewMessage, setHasNewMessage] = useState<boolean>(false);

  // Fungsi untuk membuka/menutup chat bot
  const toggleChat = (): void => {
    setIsOpen(!isOpen);
    if (!isOpen) {
      setHasNewMessage(false); // Reset notifikasi ketika chat bot dibuka
    }
  };

  // Fungsi untuk mengirim pesan
  const handleSend = (): void => {
    if (input.trim()) {
      const userMessage: Message = { text: input, sender: 'user' };
      setMessages((prevMessages) => [...prevMessages, userMessage]);
      setInput('');

      // Simulasi respons chatbot berdasarkan kata kunci
      setTimeout(() => {
        let botMessageText = 'Hallo! Untuk informasi lebih lanjut, silakan hubungi Email: Shaxyyy.03@gmail.com atau telepon: 0851-7414-5156';

        // Cek kata kunci dalam input pengguna
        const lowerInput = input.toLowerCase();
        if (lowerInput.includes('hallo') ||
            lowerInput.includes('hai') ||
            lowerInput.includes('helo') ||
            lowerInput.includes('halo')) {
          botMessageText = 'Hallo! Ada yang bisa saya bantu?';
        } else if (lowerInput.includes('bantu') ||
                   lowerInput.includes('tolong')) {
          botMessageText = 'Silakan, ada yang bisa saya bantu mengenai apa?';
        } else if (
          lowerInput.includes('shaxyyy') ||
          lowerInput.includes('ayesha') ||
          lowerInput.includes('siapa yesha') ||
          lowerInput.includes('muhamad ayesha aulia')
        ) {
          botMessageText = 'Muhamad Ayesha Aulia (Yesha) adalah seorang Front-End Developer yang berpengalaman yang berasal dari Indonesia. Ayesha juga merupakan mahasiswa Global Institute dan telah mengerjakan berbagai proyek. Lihat proyeknya di (https://yesha.ftik.my.id).';
        } else if (lowerInput.includes('terima kasih') || lowerInput.includes('makasih')) {
          botMessageText = 'Sama-sama! Semoga hari-harimu menyenankan! ';
        }

        const botMessage: Message = { text: botMessageText, sender: 'bot' };
        setMessages((prevMessages) => [...prevMessages, botMessage]);

        // Memainkan suara notifikasi
        const audio = new Audio('/audio/level-up-191997.mp3');
        audio.play();

        setHasNewMessage(true); // Menandakan ada pesan baru

        // Cek izin notifikasi
        if (Notification.permission === 'granted') {
          new Notification('Pesan Baru', {
            body: botMessageText, // Isi notifikasi
            icon: '/icon.png', // Ganti dengan icon yang sesuai
          });
        } else if (Notification.permission === 'default') {
          console.log("Notifikasi belum diizinkan, meminta izin...");
        } else {
          console.log("Notifikasi ditolak.");
        }
      }, 5000);
    }
  };

  // Fungsi untuk menangani perubahan input
  const handleInputChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setInput(e.target.value);
  };

  // Fungsi untuk menangani klik tombol send
  const handleButtonClick = (e: MouseEvent<HTMLButtonElement>): void => {
    handleSend();
  };

  // Fungsi untuk menangani event ketika tombol Enter ditekan
  const handleKeyDown = (e: KeyboardEvent<HTMLInputElement>): void => {
    if (e.key === 'Enter') {
      e.preventDefault(); // Mencegah form submit default jika ada
      handleSend(); // Mengirim pesan saat Enter ditekan
    }
  };

  // Meminta izin untuk notifikasi saat pertama kali aplikasi dimuat
  useEffect(() => {
    // Meminta izin untuk menampilkan notifikasi
    if (Notification.permission !== 'granted') {
      Notification.requestPermission().then(permission => {
        console.log("Permission notifikasi:", permission);
      });
    }
  }, []);

  return (
    <div>
      {/* Tombol untuk membuka/menutup chat bot */}
      <div
        onClick={toggleChat}
        style={{
          position: 'fixed',
          bottom: '20px',
          right: '20px',
          backgroundColor: 'blue',
          color: '#fff',
          padding: '10px',
          borderRadius: '50%',
          cursor: 'pointer',
          boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
          fontSize: '24px',
        }}
      >
        
        {/* Menambahkan indikator pesan baru */}
        {hasNewMessage && (
          <div
            style={{
              position: 'absolute',
              top: '0',
              right: '0',
              width: '12px',
              height: '12px',
              borderRadius: '50%',
              backgroundColor: 'red',
              color: '#fff',
              fontSize: '10px',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
            }}
          >
            1
          </div>
        )}
      </div>

      {/* Chat Bot Panel */}
      {isOpen && (
        <div
          style={{
            position: 'fixed',
            bottom: '70px',
            right: '20px',
            width: '350px',
            height: '400px',
            borderRadius: '8px',
            border: '1px solid #ddd',
            boxShadow: '0px 4px 8px rgba(0, 0, 0, 0.2)',
            backgroundColor: '#fff',
            display: 'flex',
            flexDirection: 'column',
            overflow: 'hidden',
          }}
        >
          <div
            style={{
              flex: 1,
              padding: '10px',
              overflowY: 'auto',
            }}
          >
            {/* Pesan */}
            {messages.map((msg, index) => (
              <div key={index} style={{ textAlign: msg.sender === 'user' ? 'right' : 'left' }}>
                <div
                  style={{
                    display: 'inline-block',
                    backgroundColor: msg.sender === 'user' ? 'blue' : '#ccc',
                    color: '#fff',
                    padding: '8px',
                    borderRadius: '10px',
                    margin: '5px 0',
                  }}
                >
                  {msg.text}
                </div>
              </div>
            ))}
          </div>
          <div
            style={{
              display: 'flex',
              padding: '10px',
              backgroundColor: 'blue',
            }}
          >
            <input
              type="text"
              value={input}
              onChange={handleInputChange}
              onKeyDown={handleKeyDown} 
              style={{
                flex: 1,
                padding: '8px',
                borderRadius: '4px',
                border: '1px solid #ccc',
              }}
            />
            <button
              onClick={handleButtonClick}
              style={{
                marginLeft: '10px',
                padding: '8px 15px',
                backgroundColor: 'red',
                color: '#fff',
                border: 'none',
                borderRadius: '4px',
                cursor: 'pointer',
              }}
            >
              Send
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default ChatBot;
