import styled from "styled-components";

export const Container = styled.section`
  margin-top: 15rem;


  h2 {
    text-align: center;
    font-size: 4.5rem; /* Increased font size */
    margin-bottom: 4rem; /* Increased bottom margin */
    color: white;
  }
    .react{
    animation: spinning 5s infinite linear;
    }
    .vue{
    animation: flip 2s infinite;
    transform-style: preserve-3d;
    }

    /* Button styles */
button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin: 10px 5px;
}

button:hover {
  background-color: #ff4081;
  transform: scale(1.05);
}

button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

/* Pagination container */
.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

/* Ensuring page number buttons are properly styled */
.pagination button {
  padding: 10px 20px;
  font-size: 16px;
  margin: 0 5px;
}

/* Optional: Change colors of the active page */
.pagination button:disabled {
  background-color: #ff4081;
}

.pagination button:hover {
  background-color: #ff4081;
  transform: scale(1.1);
}

/* Ensure More button is centered and looks good */
button.more {
  display: block;
  width: 200px;
  margin: 20px auto;
}

  .blogs {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 3 cards per row */
    grid-template-rows: auto;
    gap: 2.5rem; /* Larger gap between cards */
    padding: 1rem;

    .blog {
      padding: 2.5rem 2rem; /* Increased padding for bigger cards */
      background-color: #2b2b2b;
      border-radius: 1.5rem; /* Slightly more rounded corners */
      transition: 0.25s;
      display: flex;
      flex-direction: column;
      height: 100%;
      color: #FFF;
      box-shadow: 0 6px 8px rgba(0, 0, 0, 0.2); /* Increased shadow for depth */
      
      &:hover {
        transform: translateY(-8px); /* Slightly stronger hover effect */
        background-color: #ff4081; /* Change to pink color on hover */
      }

      img {
        width: 50px; /* Increased image size */
        margin-top: 15px; /* Adjusted spacing */
      }

      h3 {
        font-size: 2.5rem; /* Larger title size */
        margin-bottom: 1.5rem; /* Increased bottom margin */
      }

      p {
        font-size: 1.3rem; /* Increased font size for better readability */
        color: white;
        flex-grow: 1; /* Ensures text takes up the remaining space */
        margin-bottom: 2rem; /* Increased bottom margin */
      }

      footer {
        margin-top: auto;
        .blog-link {
          text-decoration: none;
          color: #fff;
          font-weight: bold;
          font-size: 1.3rem; /* Increased link size */
          transition: color 0.3s;
          
          &:hover {
            color: #ff4081;
          }
        }
      }
    }
  }

  /* Responsive for medium screens (tablets) */
  @media (max-width: 960px) {
    .blogs {
      grid-template-columns: 1fr 1fr; /* Two cards per row */
    }
  }

  /* Responsive for smaller screens (mobiles) */
  @media (max-width: 740px) {
    .blogs {
      grid-template-columns: 1fr; /* One card per row */
    }

    .blog {
      padding: 2.5r; /* Increased padding for better mobile experience */
    }

    h2 {
      font-size: 3.8rem; /* Reduced the title size for mobile */
      margin-bottom: 2.5rem; /* Adjusted spacing */
    }

    h3 {
      font-size: 2.2rem; /* Adjusted title size */
    }

    p {
      font-size: 1.2rem; /* Adjusted paragraph font size */
    }
  }
`;
